/* Filter Css  */

.Lark {
	-webkit-filter: contrast(.9);
	filter: contrast(.9);
}

.Lark:before {
	background: #22253f;
	mix-blend-mode: color-dodge;
}

.Lark:after {
	background: rgba(242, 242, 242, .8);
	mix-blend-mode: darken;
}

.Sepia img {
	-webkit-filter: sepia(0.60);
	filter: sepia(0.60);
}

.Sepia img {
	filter: sepia(100%);
	-webkit-filter: sepia(1);
	-ms-filter: sepia(100%);
}

.Street:after {
	background: -webkit-gradient(linear, left bottom, left top, from(rgba(141, 34, 158, 0.2)), to(rgba(141, 34, 158, 0.2)));
	background: -o-linear-gradient(bottom, rgba(141, 34, 158, 0.2), rgba(141, 34, 158, 0.2));
	background: linear-gradient(0deg, rgba(141, 34, 158, 0.2), rgba(141, 34, 158, 0.2));
}

.Bokeh:after {
	background: -webkit-gradient(linear, left bottom, left top, from(rgba(209, 213, 23, 0.2)), to(rgba(209, 213, 23, 0.2)));
	background: -o-linear-gradient(bottom, rgba(209, 213, 23, 0.2), rgba(209, 213, 23, 0.2));
	background: linear-gradient(0deg, rgba(209, 213, 23, 0.2), rgba(209, 213, 23, 0.2));
}

.blackWhite img {
	-webkit-filter: grayscale(1000%) brightness(1.5);
	filter: grayscale(1000%) brightness(1.5);
}


